.logo {
  display: inline-block;
  text-decoration: none;

  &__image {
    display: inline-block;
    background-size: cover;
    background-image: url(../images/logo.png);
    box-sizing: border-box;
    height: 46px;
    width: 64px;
    
    @include onTablet {
      height: 65px;
      width: 87px;
    }
  }
}
